<template>
  <div>
    <h2>asyncawait</h2>
    <h3></h3>
    <pre>
    const awaitFunc = () => {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve('awaitFunc end...')
        }, 1000)
      })
    }
    async function test () {
      console.log(new Date().getSeconds())
      const lala = await awaitFunc()
      console.log(new Date().getSeconds())
      console.log(lala)
    }

    const test1 = () => {
      console.log('test1')
    }

    test()
    test1()
    /*
    * 
    * 56
    * test1
    * 57
    * awaitFunc end...
    */
    </pre>
    <p>asyncawaitawaitasynctest1</p>
    <pre>
    const test1 = () => {
      console.log('test1')
    }
    const awaitFunc1 = () => {
      return 'awaitFunc1 end...'
    }
    async function test2 () {
      console.log(new Date().getSeconds())
      const lala = await awaitFunc1()
      console.log(new Date().getSeconds())
      console.log(lala)
    }

    test2()
    test1()
    /*
    * 
    * 9
    * test1
    * 10
    * awaitFunc1 end...
    */
    </pre>
    <p>asyncawaitasynctest1,await</p>
    <div class="from">
      <h3></h3>
      <p><a href="http://www.ruanyifeng.com/blog/2015/05/async.html" target="_new">1</a></p>
      <p><a href="https://segmentfault.com/a/1190000007535316" target="_new">2</a></p>
    </div>
  </div>
</template>

<script>
const awaitFunc = () => {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve('awaitFunc end...')
    }, 1000)
  })
}
async function test () {
  console.log(new Date().getSeconds())
  const lala = await awaitFunc()
  console.log(new Date().getSeconds())
  console.log(lala)
}
const test1 = () => {
  console.log('test1')
}

test()
test1()

const awaitFunc1 = () => {
  return 'awaitFunc1 end...'
}
async function test2 () {
  console.log(new Date().getSeconds())
  const lala = await awaitFunc1()
  console.log(new Date().getSeconds())
  console.log(lala)
}

test2()
test1()

export default {
  data () {
    return {}
  },
  mounted () {
    // test()
  }
}
</script>


